import React, { useState, useEffect } from 'react'
import { StyleSheet, Text, View, FlatList, Image, TouchableOpacity, ToastAndroid, ActivityIndicator, TextInput, Dimensions, Modal, TouchableWithoutFeedback, Animated } from 'react-native'
import { MainLookup } from '../Lookup'
import { Post } from '../HomeScreen/index'
import { useColorScheme } from 'react-native';
import colors from '../../assets/components/colors.json';
import { url } from '../components/urls'
import { useNavigation } from '@react-navigation/native'
import { UserHeader } from './index'
import Ionicons from "react-native-vector-icons/Ionicons";
import { MovieCard } from '../Navigations/BottomTabs/index'
const host = url()
const { height, width } = Dimensions.get("window")

export default function SearchScreen(props) {
    const nav = useNavigation();
    const { token } = props
    const colorScheme = useColorScheme();
    const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
    const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground
    const [more, setMore] = useState(false)
    const [genres, setGenres] = useState([])
    const [ searchs, setSearchs ] = useState([])
    const [query, setQueery] = useState('')
    useEffect(() => {
        const callBack = (r, c) => {
            if (c === 200) {
                setGenres(r)
            }
        }
        Fetch(callBack, { url: `/api/movies/geners` })
    }, [])
    const renderGenre = ({item, index}) => {
        return (
            <>
            <RenderGenreWithMovies token={token} item={item}/>
            </>
        )
    }
    const [noResult, setNoResult] = useState(false)
    const Search = () => {
        setNoResult(false)
        setSearchs([])
        const callBack = (r, c) => {
            setSearchs(r)
            if (r.length === 0) {
                setNoResult(true)
            }
        }
        Fetch(callBack, { url: `/api/movies?search=${query}` })
    }
    const renderMovie = ({item, index}) => {
        return(
        <>
            <MovieCard item={item} needWatchLater={true} />
        </>
    )}
    return (
        <>

            <View style={{ flex: 1, backgroundColor: backgroundColor, paddingTop: 50 }}>
                <TouchableOpacity onPress={() => (setMore(!more))} style={{ marginLeft: 'auto', paddingHorizontal: 20 }}>
                    <Ionicons name='search-outline' size={30} color={textColor} />
                </TouchableOpacity>
                <FlatList
                    style={{ backgroundColor: backgroundColor }}
                    data={genres} renderItem={renderGenre} />
            </View>
            <Modal
                animated
                animationType={"slide"}
                statusBarTranslucent
                visible={more}
                onDismiss={()=>(setMore(false))}
                onRequestClose={()=>(setMore(false))}
                transparent>
                <TouchableWithoutFeedback>
                    <View style={styles.overlay}>
                        <View style={[styles.container, { backgroundColor: backgroundColor }]}>
                            <View style={{ flexDirection: 'row', marginHorizontal: 10, justifyContent: 'space-between' }}>
                                <Ionicons style={{ marginRight: 10 }} onPress={() => (setMore(!more))} name='close' size={30} color={textColor} />
                                <TextInput onSubmitEditing={Search} onChangeText={val => (setQueery(val))} style={{ fontFamily: "Medium", fontSize: 17, color: textColor, width: '70%', borderColor: '#2c3e50', borderWidth: 1, borderRadius: 10, paddingHorizontal: 10 }} autoFocus autoCorrect placeholder='Search Movies' placeholderTextColor={textColor} />
                                <Ionicons style={{ marginRight: 10 }} onPress={Search} name='search-outline' size={30} color={textColor} />
                            </View>
                            {noResult ? <Text style={{ fontFamily: "Medium", fontSize: 17, color: textColor, padding: 10 }}>No result found for your search.</Text> : null}
                            <FlatList
                                style={{ backgroundColor: backgroundColor }}
                                data={searchs} renderItem={renderMovie} />
                        </View>
                    </View>
                </TouchableWithoutFeedback>
            </Modal>
        </>
    )
}
function RenderGenreWithMovies(props) {
    const {item, token} = props
    const colorScheme = useColorScheme();
    const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
    const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground
    const [ movies, setMovies ] = useState([])
    useEffect(()=>{
        const callBack = (r, c) => {
            if (c === 200) {
                setMovies(r)
            }
        }
        Fetch(callBack, { url: `/api/genre/${item.name}` })
    },[])
    const renderMovie = ({item, index}) => {
        return(
            <>
            <MovieCard needWatchLater={true} item={item} token={token} />
            </>
        )
    }
    return (
        <>
            <Text style={{ color: textColor, fontFamily: "Black", paddingLeft: 10, fontSize: 20 }}>{item.name}</Text>
            <FlatList horizontal data={movies} renderItem={renderMovie} />
        </>
    )
}

function Fetch(callBack, props) {
    const { url } = props;
    const cb = (r, c) => {
        callBack(r, c)
    }
    MainLookup(cb, { endpoint: url, method: 'GET' })
}



const styles = StyleSheet.create({
    overlay: {
        backgroundColor: 'rgba(0,0,0,0.2)',
        flex: 1,
        justifyContent: 'flex-end',
    },
    container: {
        flex: 1,
        paddingTop: 50
    },
    input: {
        padding: 5,
        margin: 5,
        fontFamily: "Medium"
    },
})
