import * as React from 'react';
import { View, ToastAndroid, TouchableOpacity, Text, Image, Dimensions, FlatList, useColorScheme, Platform } from 'react-native';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import AsyncStorage from '@react-native-async-storage/async-storage'
import Ionicons from 'react-native-vector-icons/Ionicons'
import EvilIcons from 'react-native-vector-icons/EvilIcons'
import Octicons from 'react-native-vector-icons/Octicons'
import FontAwesome5 from 'react-native-vector-icons/FontAwesome5'
import Feed from '../../HomeScreen'
import SearchScreen from '../../SearchScreen/MyProfile'
import colors from '../../../assets/components/colors.json'
import { MainLookup } from '../../Lookup'
import { useNavigation } from '@react-navigation/native'
import { createDrawerNavigator, DrawerContentScrollView, DrawerItem } from '@react-navigation/drawer'
import { url } from '../../components/urls';

const { height, width } = Dimensions.get('window')
const host = url()
const Drawer = createDrawerNavigator();
const Tab = createBottomTabNavigator();

const Home = 'Feed'
const Create = 'Make a Movie'
const Profile = 'Profile'

export function MovieCard(props) {
  const colorScheme = useColorScheme();
  const { item, token } = props;
  const needWatchLater = props.needWatchLater ? props.needWatchLater : false
  const needDeleteAction = props.needDeleteAction ? props.needDeleteAction : false
  const nav = useNavigation();
  const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
  const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground;
  const [csrfToken, setcsrfToken] = React.useState()
  React.useEffect(() => {
    fetch(`${host}/csrf`, { method: "GET", headers: { 'Content-Type': 'application/json', }, })
      .then(data => data.json()).then(data => { setcsrfToken(data.csrf) })
      .catch(error => console.error())
  }, [])

  const Lookup = (action) => {
    const cb = (r, c) => {
      console.log(r, c)
      if (Platform.OS === 'android') {
        if (c === 201) {
          if (action === 'delete_movie') {
            ToastAndroid.show("Delted movie Successfully.", 30)
          } else {
            ToastAndroid.show("Removed Movie from watch list.", 30)
          }
        } else if (c === 403) {
          ToastAndroid.show("Unable to perform action. Please try again.", 30)
        } else {
          ToastAndroid.show("Unable to perform action. Please try again later.", 30)
        }
      } else {
        if (c === 201) {
          if (action === 'delete_movie') {
            alert("Delted movie Successfully.")
          } else {
            alert("Removed Movie from watch list.")
          }
        } else if (c === 403) {
          alert("Unable to perform action. Please try again.")
        } else {
          alert("Unable to perform action. Please try again later.")
        }
      }
    }
    MainLookup(cb, { endpoint: `/api/movies/actions`, method: 'POST', token: token, csrf: csrfToken, data: { id: item.id, action } })
  }
  const RemoveWatchLater = () => {
    Lookup("remove_watch_later")
  }
  const WatchLater = () => {
    Lookup("watch_later")
  }
  const DelteMovie = () => {
    Lookup("delete_movie")
  }
  return (
    <View style={{ flexDirection: 'row', padding: 10, margin: 2, borderColor: '#2c3e50', borderWidth: 1, borderRadius: 20, alignItems: 'center' }}>
      <Image source={{ uri: item.banner }} style={{ width: 50, height: 50, borderRadius: 5, margin: 3 }} />
      <View style={{ flexDirection: 'column' }}>
        <Text style={{ fontFamily: "Black", fontSize: 13, color: textColor }}>{item.name}</Text>
        <Text style={{ fontFamily: "Bold", fontSize: 13, color: textColor }}>{item.creator.first_name} {item.creator.last_name}</Text>
        <Text style={{ fontFamily: "Medium", fontSize: 13, color: textColor }}>@{item.creator.username}</Text>
      </View>
      <View style={{ marginLeft: 'auto', justifyContent: 'space-between', flexDirection: 'column' }}>
        <TouchableOpacity onPress={() => (nav.push("Play Movie", { movie: item }))}>
          <Ionicons name='play' size={30} color={textColor} />
        </TouchableOpacity>
        {needDeleteAction ? <TouchableOpacity onPress={DelteMovie}>
          <Ionicons name='trash' size={30} color={textColor} />
        </TouchableOpacity> :
          <>
            {needWatchLater ?
              <TouchableOpacity onPress={WatchLater}>
                <Ionicons name='time-outline' size={30} color={textColor} />
              </TouchableOpacity>
              :
              <TouchableOpacity onPress={RemoveWatchLater}>
                <Ionicons name='close' size={30} color={textColor} />
              </TouchableOpacity>}
          </>
        }
      </View>
    </View>
  )
}

function CustomDrawerContent(props) {
  const colorScheme = useColorScheme();
  const { profile, token, navigation } = props
  const nav = useNavigation();
  const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
  const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground
  const [watchLaters, setWatchlaters] = React.useState([])
  const [myMovies, setMyMovies] = React.useState([])
  const [myLikingMovies, setMyLikingMovies] = React.useState([])
  const [myDisLikingMovies, setMyDisLikingMovies] = React.useState([])
  const [hideLibrary, setHideLibrary] = React.useState(false)
  const [hideMyMovies, setHideMyMovies] = React.useState(false)
  const [hideMyLiking, setHideMyLiking] = React.useState(false)
  const [hideMyDisLiking, setHideMyDisLiking] = React.useState(false)
  React.useEffect(() => {
    const cb = (res_, cd_) => {
      if (cd_ === 200) {
        setWatchlaters(res_)
      }
    }
    MainLookup(cb, { endpoint: `/api/movies/watch_later`, method: 'GET', token: token })
    const cpb = (res_, cd_) => {
      if (cd_ === 200) {
        setMyMovies(res_)
      }
    }
    MainLookup(cpb, { endpoint: `/api/movies/mine`, method: 'GET', token: token })
    const clb = (res_, cd_) => {
      if (cd_ === 200) {
        setMyLikingMovies(res_)
      }
    }
    MainLookup(clb, { endpoint: `/api/movies/my_liking`, method: 'GET', token: token })
    const cdlb = (res_, cd_) => {
      if (cd_ === 200) {
        setMyDisLikingMovies(res_)
      }
    }
    MainLookup(cdlb, { endpoint: `/api/movies/my_disliking`, method: 'GET', token: token })
  }, [])
  const Logout = () => {
    AsyncStorage.removeItem("Token")
    nav.push("Home")
  }
  return (
    <>
    <DrawerContentScrollView>
      <DrawerItem label={() => (
        <View>
          <Image style={{ width: 150, height: 150, borderRadius: 100, backgroundColor: textColor, alignSelf: 'center' }} source={{ uri: !profile.has_pfp ? `${host}${profile.pfp_url}` : profile.pfp }} />
          <Text style={{ fontFamily: 'Black', fontSize: 17, color: textColor, alignSelf: 'center' }}>{profile.first_name} {profile.last_name}</Text>
          <Text style={{ fontFamily: 'Medium', fontSize: 17, color: textColor, alignSelf: 'center' }}>@{profile.username}</Text>
          <View style={{ marginTop: 20 }}>

            {/* Watch later start */}

            <TouchableOpacity onPress={() => (setHideLibrary(!hideLibrary))} style={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'center' }}>
              <Text style={{ fontFamily: 'Medium', fontSize: 17, color: textColor, marginBottom: 5 }}>Your Watch Later Library</Text>
              <Ionicons name='caret-down-outline' color={textColor} size={20} />
            </TouchableOpacity>

            <FlatList data={hideLibrary ? watchLaters : []} renderItem={({ item, index }) => (
              <MovieCard item={item} token={token} />
            )} ListFooterComponent={() => (
              // Your Movies Start
              <>
                <TouchableOpacity onPress={() => (setHideMyMovies(!hideMyMovies))} style={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'center' }}>
                  <Text style={{ fontFamily: 'Medium', fontSize: 17, color: textColor, marginBottom: 5 }}>Your Movies</Text>
                  <Ionicons name='caret-down-outline' color={textColor} size={20} />
                </TouchableOpacity>

                <FlatList data={hideMyMovies ? myMovies : []} renderItem={({ item, index }) => (
                  <MovieCard needDeleteAction={true} item={item} token={token} />
                )} ListFooterComponent={() => (
                  // {/* Your Liked movies Start */}
                  <>
                    <TouchableOpacity onPress={() => (setHideMyLiking(!hideMyLiking))} style={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'center' }}>
                      <Text style={{ fontFamily: 'Medium', fontSize: 17, color: textColor, marginBottom: 5 }}>Your Liked Movies</Text>
                      <Ionicons name='caret-down-outline' color={textColor} size={20} />
                    </TouchableOpacity>

                    <FlatList data={hideMyLiking ? myLikingMovies : []} renderItem={({ item, index }) => (
                      <MovieCard needWatchLater={true} item={item} token={token} />
                    )} ListFooterComponent={() => (
                      <>
                        <TouchableOpacity onPress={() => (setHideMyDisLiking(!hideMyDisLiking))} style={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'center' }}>
                          <Text style={{ fontFamily: 'Medium', fontSize: 17, color: textColor, marginBottom: 5 }}>Your Disliked Movies</Text>
                          <Ionicons name='caret-down-outline' color={textColor} size={20} />
                        </TouchableOpacity>

                        <FlatList data={hideMyDisLiking ? myDisLikingMovies : []} renderItem={({ item, index }) => (
                          <MovieCard needWatchLater={true} item={item} token={token} />
                        )} />
                      </>
                    )} />
                  </>
                  // {/* Your Liked movies end */}
                )} />
              </>
              // {/* Your movies end */}
            )} />
            {/* Watch later end */}

          </View>
        </View>
      )} />
      <DrawerItem label={() => (
        <Text onPress={Logout} style={{ fontSize: 16, fontFamily: 'Bold', color: 'red' }}>Logout</Text>
      )} />
    </DrawerContentScrollView>
      <TouchableOpacity style={{ position: 'absolute', top: 50, left: 10, backgroundColor: colors.primary, borderRadius: 1000, padding: 10, alignItems: 'center', justifyContent: 'center' }} onPress={() => (navigation.closeDrawer())}>
        <Ionicons name='close' color={backgroundColor} size={25} />
      </TouchableOpacity>
    </>
  );
}

export default function HomeScreen(props) {
  const [token, setToken] = React.useState()
  const [profile, setProfile] = React.useState([]);
  React.useEffect(() => {
    AsyncStorage.getItem("Token")
      .then(res => {
        if (res) {
          setToken(res.replace('"', '').replace('"', ''))
        } else {
          console.log("Not logged in, navigations/index.js : 37")
        }
      })
  }, [])
  React.useEffect(() => {
    const cb = (res_, cd_) => {
      if (cd_ === 200) {
        setProfile(res_)
      } else if (cd_ === 403) {
        if (Platform.OS === 'android') {
          ToastAndroid.show("There was a problem Authenticating you.\nPlease log in.", 10)
        } else {
          alert("There was a problem Authenticating you.\nPlease log in.")
        }
        AsyncStorage.removeItem("Token")
        nav.reset({
          index: 0,
          routes: [{ name: 'Login' }],
        })
      } else {
        console.log(res_, cd_)
      }
    }
    MainLookup(cb, { endpoint: `/users/api/profile/me`, method: 'GET', token: token })
  }, [])
  const textColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground
  const nav = useNavigation();
  const colorScheme = useColorScheme();
  const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;

  return (
    <>
      <Drawer.Navigator screenOptions={{
        headerShown: false,
        overlayColor: colors.primary,
        drawerStyle: {
          backgroundColor: backgroundColor,
          width: width * 1.1,
          flex: 1
        },
        swipeEnabled: false
      }} drawerContent={({navigation}) => (
        <CustomDrawerContent navigation={navigation} token={token} profile={profile} />)} initialRouteName='Home Page' defaultStatus='closed'>

        <Drawer.Screen name='Home Page' children={() => (<Screens profile={profile} token={token} />)} />
      </Drawer.Navigator>
    </>
  )
}



function Screens(props) {
  const nav = useNavigation();
  const colorScheme = useColorScheme();
  const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
  const [token, setToken] = React.useState()
  React.useEffect(() => {
    AsyncStorage.getItem("Token")
      .then(res => {
        if (res) {
          setToken(res.replace('"', '').replace('"', ''))
        } else {
          console.log("Not logged in, navigations/index.js : 37")
        }
      })
  }, [])
  return (
    <>
      <Tab.Navigator initialRouteName={Home} screenOptions={({ route }) => ({
        tabBarActiveTintColor: colors.primary,
        tabBarInactiveTintColor: '#4f6d8c',
        tabBarStyle: {
          backgroundColor: colors.dBackground,
          height: 40,
        },
        tabBarIcon: ({ focused, color, size }) => {
          let iconName;
          let rn = route.name
          if (rn === Home) {
            iconName = 'home'
            return <Octicons name={iconName} size={30} color={color} />
          }
          else if (rn === Profile) {
            iconName = 'search-outline'
            return <Ionicons name={iconName} size={30} color={color} />
          }
          else if (rn === Create) {
            iconName = 'add'
            return (
              <View style={{ backgroundColor: colors.primary, paddingHorizontal: 15, borderRadius: 10 }}>
                <Ionicons name={iconName} size={30} color={color} />
              </View>
            )
          }
        },
        tabBarShowLabel: false,
      })}>
        <Tab.Screen initialParams={{ "token": token }} name={Home} options={{ headerShown: false, title: 'Topping - Home' }} component={Feed} />
        <Tab.Screen initialParams={{ "token": token }} listeners={({ navigation }) => ({
          tabPress: event => {
            event.preventDefault();
            if (token) {
              navigation.navigate("Make Movie")
            } else {
              if (Platform.OS === 'android') {
                ToastAndroid.show("You should be logged in to upload your movie.", 30)
              } else {
                alert("You should be logged in to upload your movie.")
              }
              navigation.navigate("Login")
            }
          }
        })} name={Create} options={{ headerShown: false, title: 'Topping - Publish Movie' }} children={() => (<View />)} />
        <Tab.Screen name={Profile} options={{ headerShown: false, title: 'Topping - Search Movies' }}
          children={() => (<SearchScreen token={token} />)} />
      </Tab.Navigator>
      {token ?
        <TouchableOpacity style={{ position: 'absolute', top: 50, left: 10, backgroundColor: colors.primary, borderRadius: 1000, padding: 10, alignItems: 'center', justifyContent: 'center' }} onPress={() => (nav.openDrawer())}>
          <Ionicons name='person' color={backgroundColor} size={25} />
        </TouchableOpacity> : null}
    </>
  )
}