import React, { useEffect, useState, useRef } from 'react'
import { StyleSheet, Text, View, ToastAndroid, TouchableOpacity, Dimensions, TextInput, Image, Modal, TouchableWithoutFeedback, ActivityIndicator, ScrollView, Platform } from 'react-native'
import { useNavigation } from '@react-navigation/native'
import { useColorScheme } from 'react-native';
import colors from '../../../assets/components/colors.json'
import * as ImagePicker from 'expo-image-picker';
import { Video } from 'expo-av'
import Ionicons from 'react-native-vector-icons/Ionicons'
import MaterialIcons from 'react-native-vector-icons/MaterialIcons'

const { height, width } = Dimensions.get('window')

export default function CreateMovieScreen(props) {
    const nav = useNavigation();
    const colorScheme = useColorScheme();
    const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
    const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground
    const [image, setImage] = useState(null);
    const video = useRef(null);
    const [status, setStatus] = useState({})
    const [more, setMore] = useState(false);
    const [name, setName] = useState('')
    const [desc, setDesc] = useState('')
    const [story, setStory] = useState('')
    const [genres, setGenres] = useState('')
    const pickImage = async () => {
        setMore(true)
        setImage(null)
        let result = await ImagePicker.launchImageLibraryAsync({
            mediaTypes: ImagePicker.MediaTypeOptions.Videos,
            allowsEditing: true,
            aspect: [1, 1],
            quality: 1,
            videoMaxDuration: 60 * 10
        });
        if (!result.cancelled) {
            if (result.duration > 600000) {
                setMore(false)
                if (Platform.OS === 'android') {
                    ToastAndroid.show("Can not upload video which are more than 3 minutes long.", 30)
                } else {
                    alert("Can not upload video which are more than 3 minutes long.")
                }
            } else {
                setMore(false)
                setImage(result);
            }
            console.log(result)
        } else {
            setMore(false)
        }
    };
    return (
        <>
            <Modal
                animated
                animationType={"fade"}
                statusBarTranslucent
                visible={more}
                transparent>
                <TouchableWithoutFeedback>
                    <View style={styles.overlay}>
                        <View style={[styles.overlay, { alignItems: "center", justifyContent: 'center' }]}>
                            <ActivityIndicator size="large" color={backgroundColor} />
                        </View>
                    </View>
                </TouchableWithoutFeedback>
            </Modal>
            <ScrollView style={{ padding: 20, backgroundColor: backgroundColor, flex: 1 }}>
                <View style={{ marginVertical: 20, flexDirection: 'row', justifyContent: 'space-between' }}>
                    <TouchableOpacity onPress={()=>(nav.pop())} style={{ backgroundColor: textColor, padding: 10, borderRadius: 100, alignSelf: 'center' }}>
                        <Ionicons name='close' size={30} color={backgroundColor} />
                    </TouchableOpacity>
                    {image ? 
                    <TouchableOpacity onPress={() => (nav.navigate("Make Movie Part Two", { movie: image, story, desc, name, genres }))} style={{ backgroundColor: textColor, padding: 3, borderRadius: 100, alignSelf: 'center' }}>
                        <MaterialIcons name='navigate-next' size={50} color={backgroundColor} />
                    </TouchableOpacity> : null}
                </View>
                <TouchableOpacity onPress={pickImage} style={{ height: 200, width: '100%', backgroundColor: 'gray', alignItems: 'center', justifyContent: 'center' }} >
                    {image ? <Video
                        style={{ height: '100%', width: '100%', backgroundColor: "black" }}
                        ref={video}
                        source={{
                            uri: image.uri,
                        }}
                        useNativeControls
                        resizeMode="contain"
                        isLooping
                        onPlaybackStatusUpdate={status => setStatus(() => status)}
                    /> : <>
                        <Text style={{ fontFamily: 'Bold', fontSize: 16, color: textColor, marginBottom: 10 }}>Choose Video to upload</Text>
                        <Text style={{ fontFamily: 'Regular', fontSize: 15, color: textColor }}>  Video should be lessthan 10 minutes long.</Text>
                    </>}
                </TouchableOpacity>
                <TextInput value={name} onChangeText={val=>(setName(val))} style={[styles.input, { color: textColor }]} placeholder='Name' placeholderTextColor={textColor} />
                <TextInput value={desc} onChangeText={val=>(setDesc(val))} style={[styles.input, { color: textColor }]} multiline placeholder='Description' placeholderTextColor={textColor} />
                <TextInput value={story} onChangeText={val=>(setStory(val))} style={[styles.input, { color: textColor }]} multiline placeholder='Story' placeholderTextColor={textColor} />
                <TextInput value={genres} onChangeText={val=>(setGenres(val))} style={[styles.input, { color: textColor }]} multiline placeholder='Genres (List them with commas ",")' placeholderTextColor={textColor} />
                {image ? <TouchableOpacity onPress={pickImage} style={{ marginTop: 'auto', backgroundColor: textColor, padding: 5, borderRadius: 40, alignItems: 'center', justifyContent: 'center', margin: 10 }} >
                    <Text style={{ fontFamily: 'Regular', fontSize: 20, color: backgroundColor }}>Change Video</Text>
                </TouchableOpacity> : null}
            </ScrollView>
        </>
    )
}

const styles = StyleSheet.create({
    overlay: {
        backgroundColor: 'rgba(0,0,0,0.2)',
        flex: 1,
        justifyContent: 'flex-end',
    },
    container: {
        backgroundColor: 'white',
        paddingTop: 12,
        borderTopRightRadius: 12,
        borderTopLeftRadius: 12,
        borderColor: '#2c3e50',
        borderWidth: 1,
    },
    input: {
        padding: 5, 
        margin:5,
        fontFamily: "Medium"
    },
})
