import React, { useState, useEffect, useRef } from 'react'
import { View, Text, TouchableOpacity, Image, SafeAreaView, StyleSheet, Modal, TouchableWithoutFeedback, Animated, Dimensions } from 'react-native'
import { Video } from 'expo-av'
import AsyncStorage from '@react-native-async-storage/async-storage';
import Ionicons from 'react-native-vector-icons/Ionicons'
import { MainLookup } from '../../Lookup';
import { useNavigation } from '@react-navigation/native';
import colors from '../../../assets/components/colors.json'
import { useColorScheme } from 'react-native';
import VideoPlayer from 'expo-video-player'


const { height, width } = Dimensions.get("window")

export default function DetailScreen(props) {
    const { movie } = props.route.params;
    const [inFullscreen, setInFullsreen] = useState(false)
    const [inFullscreen2, setInFullsreen2] = useState(false)
    const refVideo = useRef(null)
    const refVideo2 = useRef(null)
    const refScrollView = useRef(null)
  
    const colorScheme = useColorScheme();
    const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
    const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground
    const video = useRef(null);
    const nav = useNavigation();
    const [status, setStatus] = useState({})
    const [vid, setVid] = useState()
    const [token, setToken] = useState()
    const [more, setMore] = useState(false)
    const [playing, setPlaying] = useState(status.isPlaying ? status.isPlaying : false)
    useEffect(() => {
        AsyncStorage.getItem("Token")
            .then(res => {
                if (res) {
                    setToken(res.replace('"', '').replace('"', ''))
                    setMore(false)
                } else {
                    setMore(true)
                    console.log("Not logged in, navigations/index.js : 37")
                }
            })
    }, [])

    useEffect(() => {
        const cb = (r, c) => {
            if (c === 200) {
                setVid(r)
            }
        }
        MainLookup(cb, { endpoint: `/api/movies/${movie.id}`, method: 'GET', token: token })
    }, [])
    return (
        <>
            <SafeAreaView style={{ flex: 1, backgroundColor: backgroundColor }}>
                <View style={styles.container}>
                    <VideoPlayer
                        fullscreen={{
                            visible: false
                        }}
                        videoProps={{
                            shouldPlay: true,
                            resizeMode: Video.RESIZE_MODE_COVER,
                            //  source is required https://docs.expo.io/versions/latest/sdk/video/#props
                            source: {
                                uri: movie.video,
                            },
                            ref: refVideo,
                        }}
                        slider={{
                            maximumTrackTintColor: textColor,
                            minimumTrackTintColor: colors.primary
                        }}
                    />
                </View>
            </SafeAreaView>
            <TouchableOpacity onPress={() => (props.navigation.pop())} style={{ position: "absolute", left: 10, top: 20, flexDirection: 'row' }}>
                <Ionicons name='close' size={30} color={'white'} />
                <Text style={{ color: 'white', fontSize: 20, fontFamily: 'Bold', marginLeft: 20 }}>{movie.name}</Text>
            </TouchableOpacity>
            <Modal
                animated
                animationType={"fade"}
                statusBarTranslucent
                visible={more}
                transparent
                onDismiss={() => (setMore(false))}
                onRequestClose={() => (setMore(false))}>
                <TouchableWithoutFeedback onPress={() => (setMore(false))}>
                    <View style={styles.overlay}>
                        <Animated.View style={[styles.container, { backgroundColor: backgroundColor }]}>
                            <View style={{ width: '10%', height: 5, borderRadius: 20, alignSelf: 'center', backgroundColor: "lightgray" }} />
                            <Text style={{ fontFamily: 'Bold', alignSelf: 'center', fontSize: 20, margin: 20, color: textColor }}>You aren't logged in yet!</Text>
                            <TouchableOpacity onPress={() => (
                                setMore(false),
                                nav.reset({
                                    index: 0,
                                    routes: [{ name: 'Login' }],
                                })
                            )} style={styles.dropDownaction}>
                                <Text style={[styles.boldStyle, { color: textColor }]}>Login</Text>
                            </TouchableOpacity>
                            <TouchableOpacity onPress={() => (
                                setMore(false),
                                nav.reset({
                                    index: 0,
                                    routes: [{ name: 'Register' }],
                                })
                            )} style={styles.dropDownaction}>
                                <Text style={[styles.boldStyle, { color: textColor }]}>Register</Text>
                            </TouchableOpacity>
                            <Text style={{ fontFamily: 'Regular', alignSelf: 'center', fontSize: 15, margin: 20, color: textColor }}>You still can watch the movie.</Text>
                        </Animated.View>
                    </View>
                </TouchableWithoutFeedback>
            </Modal>
        </>
    )
}

const styles = StyleSheet.create({
    container: {
        backgroundColor: '#F5FCFF',
        flex: 1,
    },
    overlay: {
        backgroundColor: 'rgba(0,0,0,0.2)',
        flex: 1,
        justifyContent: 'flex-end',
    },
    container: {
        backgroundColor: 'white',
        paddingTop: 12,
        borderTopRightRadius: 12,
        borderTopLeftRadius: 12,
        borderColor: '#2c3e50',
        borderWidth: 1,
    },
    dropDown: {
        width: width / 2,
        position: 'absolute',
        top: 0,
        right: 50,
        borderWidth: 1,
    },
    dropDownaction: {
        padding: 5,
        paddingLeft: 10,
        alignItems: 'center',
        justifyContent: 'center',
        borderWidth: 1,
        margin: 4,
        borderColor: '#2c3e50',
        borderRadius: 10,
    },
    boldStyle: {
        fontFamily: 'Bold',
        fontSize: 17
    },
});