import React, { useState, useEffect } from 'react'
import AsyncStorage from '@react-native-async-storage/async-storage';
import IndexApp from './Screens/Navigations'
import { NavigationContainer } from '@react-navigation/native'
import AppLoading from 'expo-app-loading';
import { useFonts } from 'expo-font';
import { useColorScheme } from 'react-native';
import { Platform, TouchableWithoutFeedback, Text, View, TouchableOpacity, StyleSheet, Animated, Dimensions, SafeAreaView, Image } from 'react-native'
import colors from './assets/components/colors.json'
import { StatusBar } from 'expo-status-bar'
import firebase from "firebase";



const { width, height } = Dimensions.get('window')

const firebaseConfig = {
  apiKey: "AIzaSyDbI9lXWSQjBuPta8jCyH6SMdS32qCHh7A",
  authDomain: "topping-caf89.firebaseapp.com",
  projectId: "topping-caf89",
  storageBucket: "topping-caf89.appspot.com",
  messagingSenderId: "922660999768",
  appId: "1:922660999768:web:400a0b9f864d9cb105302d"
};

if (firebase.apps.length === 0) {
  firebase.initializeApp(firebaseConfig);
}

export default function App() {
  const colorScheme = useColorScheme();
  const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
  const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground


  let [fontsLoaded] = useFonts({
    'Poppins-Light': require("./assets/fonts/Poppins/Poppins-Light.ttf"),
    'Poppins-Regular': require("./assets/fonts/Poppins/Poppins-Regular.ttf"),
    'Poppins-Bold': require("./assets/fonts/Poppins/Poppins-Bold.ttf"),
    'Poppins-ExtraLight': require("./assets/fonts/Poppins/Poppins-ExtraLight.ttf"),
    'Poppins-BoldItalic': require("./assets/fonts/Poppins/Poppins-BoldItalic.ttf"),
    'Poppins-ExtraLightItalic': require("./assets/fonts/Poppins/Poppins-ExtraLightItalic.ttf"),
    'Italic': require("./assets/fonts/Nunito-Italic-VariableFont_wght.ttf"),
    'VariableFont_wght': require("./assets/fonts/Nunito-VariableFont_wght.ttf"),
    'Black': require("./assets/fonts/Montserrat-Black.ttf"),
    'BlackItalic': require("./assets/fonts/Montserrat-BlackItalic.ttf"),
    'Bold': require("./assets/fonts/Montserrat-Bold.ttf"),
    'BoldItalic': require("./assets/fonts/Montserrat-BoldItalic.ttf"),
    'Italic2': require("./assets/fonts/Montserrat-Italic.ttf"),
    'Light': require("./assets/fonts/Montserrat-Light.ttf"),
    'LightItalic': require("./assets/fonts/Montserrat-LightItalic.ttf"),
    'Medium': require("./assets/fonts/Montserrat-Medium.ttf"),
    'MediumItalic': require("./assets/fonts/Montserrat-MediumItalic.ttf"),
    'Regular': require("./assets/fonts/Montserrat-Regular.ttf"),
    'Thin': require("./assets/fonts/Montserrat-Thin.ttf"),
    'ThinItalic': require("./assets/fonts/Montserrat-ThinItalic.ttf"),
    'Italic3': require("./assets/fonts/SourceSans3-Italic-VariableFont_wght.ttf"),
    'VariableFont_wght2': require("./assets/fonts/SourceSans3-VariableFont_wght.ttf"),
  })


  if (!fontsLoaded) {
    return (
      <AppLoading />
    )
  } else {
    if (Platform.OS === 'web') {
      return (
        <View style={{ flex: 1, backgroundColor: backgroundColor }}>
          <Image style={{ width: 250, height: 50, tintColor: textColor, alignSelf: 'center' }} source={require('./assets/logos/6.png')} />
          <View style={{ flex: 1, width: width / 3, alignSelf: 'center' }}>
            <NavigationContainer>
              <IndexApp />
            </NavigationContainer>
          </View>
        </View>
      )
    } else {
      return (
        <>
          <StatusBar networkActivityIndicatorVisible />
          <NavigationContainer>
            <IndexApp />
          </NavigationContainer>
        </>
      );
    }
  }
}