import React, { useState, useEffect } from 'react'
import { View, Text, FlatList, StyleSheet, Image, Dimensions, ImageBackground, Platform, Pressable, TextInput, TouchableOpacity, ToastAndroid } from 'react-native'
import styles from '../../assets/components/styles'
import colors from '../../assets/components/colors.json'
import { useColorScheme } from 'react-native';
import { useNavigation } from '@react-navigation/native'
import { url } from '../components/urls'
import { MainLookup } from '../Lookup'
import MovieActions from './components/MovieActions'

const host = url();
const { width, height } = Dimensions.get('window')
// const movieWidth = width;

// if (Platform.OS === 'web') { 
//     movieWidth = width / 3
// }
export default function Feed(props) {
    const { token } = props?.route?.params;
    const nav = useNavigation();
    const [movies, setMovies] = useState([])
    const colorScheme = useColorScheme();
    useEffect(() => {
        const cb = (r, c) => {
            if (c === 200) {
                setMovies(r)
            } else {
                console.log(r, c)

            }
        }
        MainLookup(cb, { endpoint: `/api/movies`, method: 'GET' })
    }, [])
    const [screenWidth, setScreenWidth] = useState(width)
    useEffect(() => {
        // setInterval(() => {
            console.log(Dimensions.get('window').width)
            setScreenWidth(Dimensions.get('window').width)
        // }, 1000)
    },[Dimensions.get('window').width])
    return (
        <>
            <FlatList
                style={{ backgroundColor: colors.dBackground }}
                data={movies}
                renderItem={({ item, index }) => (
                    <MovieCard item={item} />
                )}
                numColumns={1}
                horizontal={Platform.OS === 'web' ? false : true}
                snapToInterval={width}
                decelerationRate={'fast'}
                overScrollMode={'never'}
                showsVerticalScrollIndicator={false}
                snapToAlignment={'start'}
            />
        </>
    )
}

export function MovieCard(props) {
    const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground
    const colorScheme = useColorScheme();
    const { item } = props;
    const nav = useNavigation();
    
    return (
        <ImageBackground blurRadius={30} source={{ uri: item.banner }} style={{ flex: 1, width: Platform.OS === 'web' ? '100%' : width }}>
            <ImageBackground style={{ height: height / 2, width: '100%', alignItems: 'center', justifyContent: 'center' }} source={{ uri: item.banner }}>
                <Image style={{ height: 200, width: 200, borderRadius: 100, marginTop: 'auto', marginBottom: 20 }} source={{ uri: item.cover_image }} />
            </ImageBackground>
            <View style={{ alignSelf: 'center', padding: 5 }}>
                <Text style={{ color: textColor, fontSize: 40, fontFamily: 'Black' }}>{item.name}</Text>
                <Text style={{ color: textColor, fontSize: 20, fontFamily: 'Bold' }}>{truncate(item.description, 10)}...</Text>
                <Text style={{ color: textColor, fontSize: 15, fontFamily: 'Bold' }}>{truncate(item.story, 10)}...</Text>
                <Text style={{ color: textColor, fontSize: 15, fontFamily: 'Regular' }}>A movie by {item.creator.first_name} {item.creator.last_name} @{item.creator.username}</Text>
            </View>
            <MovieActions item={item} />
            <Text style={{ color: textColor, fontFamily: 'Black', alignSelf: 'center' }}>{duration(item.duration)}</Text>
        </ImageBackground>
    )
}
function truncate(str, no_words) {
    return str.split(" ").splice(0, no_words).join(" ");
}
function duration(str) {
    var dur = `${str / 60}`
    return new Date(str * 1000).toISOString().substr(11, 8);
}

