import React, { useState, useEffect } from 'react'
import { useNavigation } from '@react-navigation/native'
import { createStackNavigator } from '@react-navigation/stack'
import AsyncStorage from '@react-native-async-storage/async-storage';
import { LoginComponent } from '../Auth/Login'
import HomeScreen from './BottomTabs';
import colors from '../../assets/components/colors.json'
import { useColorScheme } from 'react-native';
import DetailScreen from '../HomeScreen/DetailSceen/Play'
import { RegisterComponent } from '../Auth/Register'
import { Modal, TouchableWithoutFeedback, Text, View, TouchableOpacity, StyleSheet, Animated, Dimensions } from 'react-native'
import CreateMovieScreen from '../HomeScreen/MakeMovie/index'
import PartB from '../HomeScreen/MakeMovie/b'

const { width, height } = Dimensions.get('window')
const Stack = createStackNavigator();

export default function IndexApp() {
    const [more, setMore] = useState(false)
    const colorScheme = useColorScheme();
    const nav = useNavigation();
    const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
    const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground

    const [token, setToken] = useState()

    useEffect(() => {
        AsyncStorage.getItem("Token")
            .then(res => {
                if (res) {
                    setToken(res.replace('"', '').replace('"', ''))
                    setMore(false)
                } else {
                    setMore(true)
                    console.log("Not logged in, navigations/index.js : 37")
                }
            })
    }, [])
    return (
        <>
            <Stack.Navigator initialRouteName={'Home'}>
                <Stack.Screen options={{ headerShown: false }} name='Home' children={HomeScreen} />
                <Stack.Screen options={{ headerShown: false }} name='Login' component={LoginComponent} />
                <Stack.Screen options={{ headerShown: false }} name='Register' component={RegisterComponent} />
                <Stack.Screen options={{ headerShown: false }} name='Play Movie' component={DetailScreen} />
                <Stack.Screen options={{ headerShown: false }} name='Make Movie' component={CreateMovieScreen} />
                <Stack.Screen options={{ headerShown: false }} name='Make Movie Part Two' component={PartB} />
            </Stack.Navigator>
            <Modal
                animated
                animationType={"fade"}
                statusBarTranslucent
                visible={more}
                transparent
                onDismiss={() => (setMore(false))}
                onRequestClose={() => (setMore(false))}>
                <TouchableWithoutFeedback onPress={() => (setMore(false))}>
                    <View style={styles.overlay}>
                        <Animated.View style={[styles.container, { backgroundColor: backgroundColor }]}>
                            <View style={{ width: '10%', height: 5, borderRadius: 20, alignSelf: 'center', backgroundColor: "lightgray" }} />
                            <Text style={{ fontFamily: 'Bold', alignSelf: 'center', fontSize: 20, margin: 20, color: textColor }}>Welcome to Topping</Text>
                            <TouchableOpacity onPress={() => (
                                setMore(false),
                                nav.reset({
                                    index: 0,
                                    routes: [{ name: 'Login' }],
                                })
                            )} style={styles.dropDownaction}>
                                <Text style={[styles.boldStyle, { color: textColor }]}>Login</Text>
                            </TouchableOpacity>
                            <TouchableOpacity onPress={() => (
                                setMore(false),
                                nav.reset({
                                    index: 0,
                                    routes: [{ name: 'Register' }],
                                })
                            )} style={styles.dropDownaction}>
                                <Text style={[styles.boldStyle, { color: textColor }]}>Register</Text>
                            </TouchableOpacity>
                        </Animated.View>
                    </View>
                </TouchableWithoutFeedback>
            </Modal>
        </>
    )
}

const styles = StyleSheet.create({
    overlay: {
        backgroundColor: 'rgba(0,0,0,0.2)',
        flex: 1,
        justifyContent: 'flex-end',
    },
    container: {
        backgroundColor: 'white',
        paddingTop: 12,
        borderTopRightRadius: 12,
        borderTopLeftRadius: 12,
        borderColor: '#2c3e50',
        borderWidth: 1,
    },
    dropDown: {
        width: width / 2,
        position: 'absolute',
        top: 0,
        right: 50,
        borderWidth: 1,
    },
    dropDownaction: {
        padding: 5,
        paddingLeft: 10,
        alignItems: 'center',
        justifyContent: 'center',
        borderWidth: 1,
        margin: 4,
        borderColor: '#2c3e50',
        borderRadius: 10,
    },
    boldStyle: {
        fontFamily: 'Bold',
        fontSize: 17
    },
})