import React, { useState, useEffect } from 'react'
import AsyncStorage from '@react-native-async-storage/async-storage';
import { url } from '../components/urls'
import { useNavigation } from '@react-navigation/native'
import { MainLookup } from '../Lookup'
import colors from '../../assets/components/colors.json'
import collours from '../../assets/components/colors.json'
import { useColorScheme } from 'react-native';
import styles from './styles'
import { Platform, Modal, Animated, StyleSheet, TextInput, Dimensions, ScrollView, Text, View, Image, ToastAndroid, ActivityIndicator, TouchableOpacity } from 'react-native'

const { width, height } = Dimensions.get("screen")
const host = url()

export function LoginComponent(props) {
  const colorScheme = useColorScheme();
  const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
  const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground
  const [username, setusername] = useState()
  const [more, setMore] = useState(false)
  const nav = useNavigation();
  const [password, setPassword] = useState()
  const [loading, setloading] = useState(false)
  const [Required, setRequired] = useState(false)
  const [Incorrect, setIncorrect] = useState(false)
  const [csrfToken, setcsrfToken] = useState()
  useEffect(() => {
    fetch(`${host}/csrf`, {
      method: "GET",
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(data => data.json()).then(
        data => {
          setcsrfToken(data.csrf)
        })
      .catch(error => console.error())
  }, [])
  const storeData = async (value) => {
    try {
      const jsonValue = JSON.stringify(value)
      await AsyncStorage.setItem('Token', jsonValue)
    } catch (e) {
      console.log(e)
    }
  }
  const mycallback = (response, status) => {
    console.log(response, status)
    setloading(false)
    if (status === 200) {
      console.log(response.token)
      setIncorrect(false)
      setRequired(false)
      storeData(response.token)
      nav.reset({
        index: 0,
        routes: [{ name: 'Home', params: { 'token': response.token } }],
      })
      //   reloadApp()
    } else if (status === 400) {
      if (response.non_field_errors) {
        setIncorrect(true)
        if (Platform.OS === 'android') {
          ToastAndroid.show("Make sure to check your capitaliztion. both fields are case sensitive.", 1000)
        } else {
          alert("Make sure to check your capitaliztion. both fields are case sensitive.")
        }
        setRequired(false)
      }
    } else {
      console.log(status)
    }
    if (status === 403) {
      setIncorrect(false)
      setRequired(false)
      if (Platform.OS === 'android') {
        ToastAndroid.show("Network Error, Please try again.", 50)
      }
    }
  }
  const SignIn = (event) => {
    if (username && password) {
      setloading(true)
      setRequired(false)
      MainLookup(
        mycallback, {
        // token:token,
        csrf: csrfToken,
        endpoint: '/users/accounts/api/auth/login',
        method: 'POST',
        data: {
          username: username,
          password: password,
        }
      }
      )
    } else {
      setIncorrect(false)
      setRequired(true)
    }
  }
  return (
    <>
      <ScrollView>
        <View style={[styles.container, { backgroundColor: backgroundColor, paddingBottom: height / 2 }]}>
          <Image style={{ width: 500, height: 500, tintColor: textColor, alignSelf: 'center' }} source={require('../../assets/logos/7.png')} />
          <TextInput placeholderTextColor={textColor} value={username} onChangeText={(val) => (setusername(val))} style={[styles.input, { color: textColor, fontFamily: 'Black' }]} placeholder='Username' />
          <TextInput placeholderTextColor={textColor} value={password} onChangeText={(val) => (setPassword(val))} style={[styles.input, { color: textColor }]} placeholder='Password' secureTextEntry={true} />
          <TouchableOpacity onPress={SignIn} style={[styles.Register, { borderColor: '#2c3e50', borderWidth: 2 }]}>
            {loading ? <ActivityIndicator size="small" color={textColor} /> : <Text style={[styles.registerText, { color: textColor }]}>Login</Text>}
          </TouchableOpacity>
          <TouchableOpacity onPress={() => (setMore(true))} style={{ alignItems: 'center', justifyContent: 'center' }}>
            <Text style={{ color: textColor, fontFamily: 'Regular', fontSize: 15 }}>Have an account on Chacha?</Text>
            <Text style={{ color: textColor, fontFamily: 'Regular', fontSize: 15 }}>You can use your Chacha account to login.</Text>
            <Text style={{ color: textColor, fontFamily: 'Bold', fontSize: 15 }}>Learn more</Text>
          </TouchableOpacity>
          <TouchableOpacity onPress={() => (
            nav.push("Register")
          )} style={[styles.Register]}>
            <Text style={{ color: textColor, fontFamily: 'Light', fontSize: 15 }}>Don't have an account? <Text style={{ fontFamily: 'Bold' }}>Register.</Text> </Text>
          </TouchableOpacity>
        </View>
      </ScrollView>
      <View style={{ position: 'absolute', top: 0, width: '90%', alignItems: 'center', justifyContent: 'center', marginTop: 50, alignSelf: 'center' }}>
        {Incorrect ? <View style={styles.warning}>
          <Text style={{ fontSize: 15, color: 'white', fontFamily: 'Black' }}>Username and password didn't match</Text>
        </View> : null}
        {Required ? <View style={styles.warning}>
          <Text style={{ fontSize: 15, color: 'white', fontFamily: 'Black' }}>Please Fill both of the fields correctly</Text>
        </View> : null}
      </View>
      <Modal
        animated
        animationType={"fade"}
        statusBarTranslucent
        visible={more}
        transparent
        onDismiss={() => (setMore(false))}
        onRequestClose={() => (setMore(false))}>
        <View style={styles.overlay}>
          <Animated.View style={[styles.modalContainer, { backgroundColor: backgroundColor, padding: 10 }]}>
            <ScrollView>
              <View style={{ width: '10%', height: 5, borderRadius: 20, alignSelf: 'center', backgroundColor: "lightgray" }} />
              <Text style={{ color: textColor, fontFamily: 'Bold', fontSize: 15, alignSelf: 'center' }}>What is chacha?</Text>
              <Text style={{ color: textColor, fontFamily: 'Regular', fontSize: 15, alignSelf: 'center' }}>Chacha is a new Social Media platform based in Africa.
                It's goals are to bring African people closer and make people heard.</Text>
              <Text style={{ color: textColor, fontFamily: 'Bold', fontSize: 15, alignSelf: 'center', margin: 5 }}>What is the relation of Chacha and Topping?</Text>
              <Text style={{ color: textColor, fontFamily: 'Regular', fontSize: 15, alignSelf: 'center' }}>Topping is made in the same user base with Chacha.
                Meaning if you have an account on Chacha, you will have access to Topping. and vise versa.</Text>
              <Text style={{ color: textColor, fontFamily: 'Bold', fontSize: 15, alignSelf: 'center', margin: 5 }}>Does this mean I can login with my Chacha account?</Text>
              <Text style={{ color: textColor, fontFamily: 'Regular', fontSize: 15, alignSelf: 'center' }}>Yes. That is right. once you create an account in Topping. You can have access to Topping, as well as Chacha.</Text>
              <Text onPress={() => (nav.push("Login"))} style={{ color: textColor, fontFamily: 'Bold', fontSize: 15, alignSelf: 'center', margin: 20 }}>Log me in!</Text>
            </ScrollView>
            <TouchableOpacity onPress={() => (setMore(false))} style={{ position: 'absolute', top: 0, left: 10 }}><Text style={{ fontFamily: 'Bold', fontSize: 20, color: textColor }}>X</Text></TouchableOpacity>
          </Animated.View>
        </View>
      </Modal>
    </>
  )
}
