import React, { useState, useEffect } from 'react'
import { View, Text, ToastAndroid, StyleSheet, Dimensions, Image, Pressable, TouchableOpacity } from 'react-native'
import Icon from "react-native-vector-icons/Ionicons";
import styles from '../../../assets/components/styles'
import colors from '../../../assets/components/colors.json'
import { useColorScheme } from 'react-native';


const { width, height } = Dimensions.get("screen")

export function StatusAuthorProfile(props) {
    const colorScheme = useColorScheme();
    const { status } = props
    const h3Style = colorScheme === 'light' ? styles.lTextH1 : styles.dTextH1;
    const needUsername = props.needUsername ? props.needUsername : false
    return (
        <>
            <Text style={h3Style}>{status.author.first_name} {status.author.last_name}</Text>
            {!status.author.first_name && !status.author.last_name || needUsername ? <Text style={h3Style}>@{status.author.username}</Text> : null}
            {status.author.verified ? <Icon
                name="md-checkmark-circle"
                color="#1d9bf0"
                size={15}
            /> : null}
        </>
    )
}
