import { url } from '../components/urls'
import AsyncStorage from '@react-native-async-storage/async-storage';
import { ToastAndroid, Platform } from 'react-native'
const host = url()

export function MainLookup(callback, props) {
  const { method, endpoint, data, token, csrf } = props
  if (Platform.OS === 'web') {
    let tokk;
    const cb = (e, r) => {
      if (r) {
        tokk = r.replace('"', '').replace('"', '')
      }
    }
    AsyncStorage.getItem("Token", cb)
    let jsonData;
    if (data) {
      jsonData = JSON.stringify(data)
    }
    const xhr = new XMLHttpRequest()
    const url = `${host}${endpoint}`
    xhr.responseType = "json"
    xhr.open(method, url)
    xhr.setRequestHeader("Content-Type", "application/json")
    if (tokk) {
      xhr.setRequestHeader('Authorization', `Token ${tokk}`)
    }
    if (!Platform.OS === 'web') {
      xhr.setRequestHeader("Referer", host)
    }
    if (csrf) {
      // console.log(csrf.csrf)
      xhr.setRequestHeader("X-CSRFToken", csrf)
    }
    xhr.onload = function () {
      callback(xhr.response, xhr.status)
    }
    xhr.onerror = function (e) {
      console.log(e)
      callback({ "message": "The request was an error" }, 400)
    }
    xhr.send(jsonData)
  } else {
    let jsonData;
    if (data) {
      jsonData = JSON.stringify(data)
    }
    const xhr = new XMLHttpRequest()
    const url = `${host}${endpoint}`
    xhr.responseType = "json"
    xhr.open(method, url)
    xhr.setRequestHeader("Content-Type", "application/json")
    if (token) {
      xhr.setRequestHeader('Authorization', `Token ${token}`)
    }
    if (csrf) {
      xhr.setRequestHeader("Referer", host)
      xhr.setRequestHeader("X-CSRFToken", csrf)
    }
    xhr.onload = function () {
      var response = xhr.response
      var statusCode = xhr.status
      callback(response, statusCode)
    }
    xhr.send(jsonData)
  }
}