import React, { useState, useEffect } from 'react'
import { View, Text, Modal, Animated, TouchableWithoutFeedback, Dimensions, StyleSheet, Share, TextInput, TouchableOpacity, ToastAndroid, Platform } from 'react-native'
// import styles from '../../../assets/components/styles'
import colors from '../../../assets/components/colors.json'
import { useColorScheme } from 'react-native';
import { useNavigation } from '@react-navigation/native'
import { url } from '../../components/urls'
import { MainLookup } from '../../Lookup'
import Ionicons from 'react-native-vector-icons/Ionicons'
import AntDesign from 'react-native-vector-icons/AntDesign'
import AsyncStorage from '@react-native-async-storage/async-storage';

const host = url()
const { width, height } = Dimensions.get('window')

export default function MovieActions(props) {
    const nav = useNavigation()
    const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
    const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground
    const colorScheme = useColorScheme()
    const { item } = props
    const [more, setMore] = useState(false)
    const [token, setToken] = useState()
    useEffect(() => {
        AsyncStorage.getItem("Token")
            .then(res => {
                if (res) {
                    setToken(res.replace('"', '').replace('"', ''))
                } else {
                    console.log("Not logged in, navigations/index.js : 37")
                }
            })
    }, [])
    const [csrfToken, setcsrfToken] = useState()
    useEffect(() => {
        // setMore(false)
        fetch(`${host}/csrf`, { method: "GET", headers: { 'Content-Type': 'application/json', }, })
            .then(data => data.json()).then(data => { setcsrfToken(data.csrf) })
            .catch(error => console.error())
    }, [])
    const [hasLiked, setHasLiked] = useState(item.hasLiked)
    const [hasDisLiked, setHasDisLiked] = useState(item.hasDisLiked)
    const [likes, setlikes] = useState(item.likes.length)
    const [dislikes, setDislikes] = useState(item.dislikes.length)
    const Lookup = (action) => {
        if (token) {
            if (action === 'like') {
                setHasLiked(true)
                setHasDisLiked(false)
            } else if (action === 'dislike') {
                setHasLiked(false)
                setHasDisLiked(true)
            }
            const cb = (r, c) => {
                console.log(r, c)
                if (c === 201) {
                    if (action === 'like' || action === 'dislike') {
                        setlikes(r.likes.length)
                        setDislikes(r.dislikes.length)
                        setHasLiked(r.hasLiked)
                        setHasDisLiked(r.hasDisLiked)
                    } else if (action === 'watch_later') {
                        if (Platform.OS === 'android') {
                            ToastAndroid.show("Successfully added movie to your watch later library.", 40)
                        } else {
                            alert("Successfully added movie to your watch later library.")
                        }
                    } else {
                        if (Platform.OS === 'android') {
                            ToastAndroid.show("Action sucessfull.", 40)
                        } else {
                            alert("Action sucessfull.")
                        }
                    }
                } else if (c === 403) {
                    if (Platform.OS === 'android') {
                        ToastAndroid.show("Unable to perform action. Please try again.", 30)
                    } else {
                        alert("Unable to perform action. Please try again.")
                    }
                } else {
                    if (Platform.OS === 'android') {
                        ToastAndroid.show("Unable to perform action. Please try again later.", 30)
                    } else {
                        alert("Unable to perform action. Please try again later.")
                    }
                }
            }
            MainLookup(cb, { endpoint: `/api/movies/actions`, method: 'POST', token: token, csrf: csrfToken, data: { id: item.id, action } })
        } else {
            setMore(true)
        }
    }
    const Like = () => {
        Lookup("like")
    }
    const DisLike = () => {
        Lookup("dislike")
    }
    const WatchLater = () => {
        Lookup("watch_later")
    }
    const ShareMovie = async () => {
        try {
            const result = await Share.share({
                message: `Watch "${item.name}" on Toppings, It's really Good!`,
            });
            if (result.action === Share.sharedAction) {
                if (result.activityType) {
                } else {
                    // shared
                }
            } else if (result.action === Share.dismissedAction) {
                // dismissed
            }
        } catch (error) {
            if (Platform.OS === 'android') {
                ToastAndroid.show(error.message, 30);
            }
        }
    }
    return (
        <>
            <View style={{ marginTop: 'auto', flexDirection: 'row', justifyContent: 'space-around' }}>
                <TouchableOpacity onPress={Like}>
                    <AntDesign name={hasLiked ? 'like1' : 'like2'} size={30} color={colors.primary} />
                </TouchableOpacity>
                <TouchableOpacity onPress={DisLike}>
                    <AntDesign name={hasDisLiked ? 'dislike1' : 'dislike2'} size={30} color={colors.primary} />
                </TouchableOpacity>
                <TouchableOpacity onPress={ShareMovie}>
                    <Ionicons name='share-social-outline' size={30} color={colors.primary} />
                </TouchableOpacity>
                <TouchableOpacity onPress={() => (nav.push("Play Movie", { movie: item }))}>
                    <Ionicons name='play-outline' size={30} color={colors.primary} />
                </TouchableOpacity>
                <TouchableOpacity onPress={WatchLater}>
                    <Ionicons name='time-outline' size={30} color={colors.primary} />
                </TouchableOpacity>
            </View>
            <View style={{ marginTop: 'auto', flexDirection: 'row', justifyContent: 'space-between', marginBottom: 10, padding: 10 }}>
                <Text style={{ color: textColor, fontFamily: 'Black' }}> {item.views} views</Text>
                <Text style={{ color: textColor, fontFamily: 'Black' }}> {likes} likes</Text>
                <Text style={{ color: textColor, fontFamily: 'Black' }}> {dislikes} dislikes</Text>
            </View>
            <Text style={{ color: textColor, fontFamily: 'Black' }} > genres: {item.genre.map(item => (
                <Text style={{ color: textColor, fontFamily: 'Regular' }} key={item.id}> {item.name}</Text>
            ))}</Text>
            <Modal
                animated
                animationType={"fade"}
                statusBarTranslucent
                visible={more}
                transparent
                onDismiss={() => (setMore(false))}
                onRequestClose={() => (setMore(false))}>
                <TouchableWithoutFeedback onPress={() => (setMore(false))}>
                    <View style={styles.overlay}>
                        <Animated.View style={[styles.container, { backgroundColor: backgroundColor }]}>
                            <View style={{ width: '10%', height: 5, borderRadius: 20, alignSelf: 'center', backgroundColor: "lightgray" }} />
                            <Text style={{ fontFamily: 'Bold', alignSelf: 'center', fontSize: 20, margin: 20, color: textColor }}>You should be logged in to Like or share video.</Text>
                            <TouchableOpacity onPress={() => (
                                setMore(false),
                                nav.reset({
                                    index: 0,
                                    routes: [{ name: 'Login' }],
                                })
                            )} style={styles.dropDownaction}>
                                <Text style={[styles.boldStyle, { color: textColor }]}>Login</Text>
                            </TouchableOpacity>
                            <TouchableOpacity onPress={() => (
                                setMore(false),
                                nav.reset({
                                    index: 0,
                                    routes: [{ name: 'Register' }],
                                })
                            )} style={styles.dropDownaction}>
                                <Text style={[styles.boldStyle, { color: textColor }]}>Register</Text>
                            </TouchableOpacity>
                        </Animated.View>
                    </View>
                </TouchableWithoutFeedback>
            </Modal>
        </>
    )
}
const styles = StyleSheet.create({
    overlay: {
        backgroundColor: 'rgba(0,0,0,0.2)',
        flex: 1,
        justifyContent: 'flex-end',
    },
    container: {
        backgroundColor: 'white',
        paddingTop: 12,
        borderTopRightRadius: 12,
        borderTopLeftRadius: 12,
        borderColor: '#2c3e50',
        borderWidth: 1,
    },
    dropDown: {
        width: width / 2,
        position: 'absolute',
        top: 0,
        right: 50,
        borderWidth: 1,
    },
    dropDownaction: {
        padding: 5,
        paddingLeft: 10,
        alignItems: 'center',
        justifyContent: 'center',
        borderWidth: 1,
        margin: 4,
        borderColor: '#2c3e50',
        borderRadius: 10,
    },
    boldStyle: {
        fontFamily: 'Bold',
        fontSize: 17
    },
})