import React, { useState, useEffect } from 'react'
import { StyleSheet, Text, View, ScrollView, TouchableOpacity, Dimensions, ImageBackground, ActivityIndicator, ToastAndroid, Modal, TouchableWithoutFeedback, Animated, Platform } from 'react-native'
import * as ImagePicker from 'expo-image-picker';
import colors from '../../../assets/components/colors.json'
import { useColorScheme } from 'react-native';
import { useNavigation } from '@react-navigation/native'
import Ionicons from 'react-native-vector-icons/Ionicons'
import AsyncStorage from '@react-native-async-storage/async-storage';
import { url } from '../../components/urls';
import { MainLookup } from '../../Lookup'
import firebase from 'firebase';

require("firebase/firestore");
require("firebase/firebase-storage")

const { width, height } = Dimensions.get("window")
const host = url();

const uploadImage = async (name, callBack, url) => {
    const response = await fetch(url);
    const path = `community/${name}/${Math.random().toString(36)}`
    const blob = await response.blob();
    const task = firebase
        .storage()
        .ref()
        .child(path)
        .put(blob);
    const taskProgress = snapshot => {
        callBack(`transferred: ${snapshot.bytesTransferred}`, 200)
    }
    const taskCompleted = () => {
        task.snapshot.ref.getDownloadURL().then((snapshot) => {
            callBack(snapshot, 201);
        })
    }
    const taskError = snapshot => {
        console.log(snapshot);
        callBack(snapshot, 400)
    }
    task.on("state_changed", taskProgress, taskError, taskCompleted);
}

export default function PartB(props) {
    const nav = useNavigation();
    const { movie, story, desc, name, genres } = props?.route?.params;
    const [coverImg, setCoverImg] = useState()
    const [bannerImg, setBannerImg] = useState()
    const colorScheme = useColorScheme();
    const backgroundColor = colorScheme === 'light' ? colors.lBackground : colors.dBackground;
    const textColor = colorScheme === 'light' ? colors.dBackground : colors.lBackground
    const [csrfToken, setcsrfToken] = useState()
    const [token, setToken] = useState()
    const [more, setMore] = useState(false)
    useEffect(() => {
        AsyncStorage.getItem("Token")
            .then(res => {
                if (res) {
                    setToken(res.replace('"', '').replace('"', ''))
                } else {
                    console.log("Not logged in, MakeMovie/b.js : 56")
                }
            })
    }, [])

    useEffect(() => {
        // setMore(false)
        fetch(`${host}/csrf`, {
            method: "GET",
            headers: {
                'Content-Type': 'application/json',
            },
        })
            .then(data => data.json()).then(
                data => {
                    setcsrfToken(data.csrf)
                })
            .catch(error => console.error())
    }, [])

    const pickCoverImage = async () => {
        let result = await ImagePicker.launchImageLibraryAsync({
            mediaTypes: ImagePicker.MediaTypeOptions.Images,
            allowsEditing: true,
            aspect: [1, 1],
            quality: 1,
        });
        if (!result.cancelled) {
            setCoverImg(result);
        }
    };

    const pickBannerImage = async () => {
        let result = await ImagePicker.launchImageLibraryAsync({
            mediaTypes: ImagePicker.MediaTypeOptions.Images,
            allowsEditing: true,
            aspect: [1, 1],
            quality: 1,
        });
        if (!result.cancelled) {
            setBannerImg(result);
        }
    };
    const PublishMovie = () => {
        setMore(true)
        if (bannerImg && coverImg && name && story && desc) {
            const BannerCb = (Br, Bc) => {
                console.log(Br, Bc, 88)
                if (Bc === 201) {
                    const CoverCb = (Cr, CC) => {
                        console.log(Cr, CC)
                        if (CC === 201) {
                            const cb = (response, code) => {
                                console.log(response, code)
                                setMore(false)
                                if (code === 201) {
                                    if (Platform.OS === 'android') {
                                        ToastAndroid.show("Movie Uploaded Successfuly.", 30)
                                    } else {
                                        alert("Movie Uploaded Successfuly.")
                                    }
                                    nav.reset({
                                        index: 0,
                                        routes: [{ name: 'Home', params: { 'token': response.token } }],
                                    })
                                } else if (code === 400) {
                                    if (response.name) {
                                        if (Platform.OS === 'android') {
                                            ToastAndroid.show("That name is taken, Please try another one.", 50)
                                        } else {
                                            alert("That name is taken, Please try another one.")
                                        }
                                    }
                                }
                                else {
                                    if (Platform.OS === 'android') {
                                        ToastAndroid.show("Movie wasn't Uploaded. Please try again.", 30)
                                    } else {
                                        alert("Movie wasn't Uploaded. Please try again.")
                                    }
                                }
                            }
                            MainLookup(cb, {
                                endpoint: `/api/make-movies`, csrf: csrfToken, token: token, method: 'POST', data: {
                                    name: name,
                                    description: desc,
                                    story: story,
                                    video: movie.uri,
                                    cover_image: Cr,
                                    banner: Br,
                                    duration: movie.duration,
                                    width: movie.width,
                                    height: movie.height,
                                    genres
                                }
                            })
                        }
                    }
                    uploadImage(name, CoverCb, coverImg.uri)
                }
            }
            uploadImage(name, BannerCb, bannerImg.uri)
        }
    }
    return (
        <>
            <Modal
                animated
                animationType={"fade"}
                statusBarTranslucent
                visible={more}
                transparent>
                <TouchableWithoutFeedback>
                    <View style={styles.overlay}>
                        <View style={[styles.overlay, { alignItems: "center", justifyContent: 'center' }]}>
                            <ActivityIndicator size="large" color={backgroundColor} />
                        </View>
                    </View>
                </TouchableWithoutFeedback>
            </Modal>
            <ImageBackground blurRadius={30} source={{ uri: bannerImg ? bannerImg.uri : null }} style={{ flex: 1, padding: 10, backgroundColor: backgroundColor }}>
                <View style={{ marginVertical: 20, flexDirection: 'row', justifyContent: 'space-between' }}>
                    <TouchableOpacity onPress={() => (nav.pop())} style={{ backgroundColor: textColor, padding: 10, borderRadius: 100, alignSelf: 'center' }}>
                        <Ionicons name='close' size={30} color={backgroundColor} />
                    </TouchableOpacity>
                </View>
                <TouchableOpacity onPress={pickBannerImage} style={{ alignItems: 'center', justifyContent: 'center' }}>

                    <ImageBackground style={{ height: height / 2, width: '100%', alignItems: 'center', justifyContent: 'center', backgroundColor: colors.primary }} source={{ uri: bannerImg ? bannerImg.uri : null }}>

                        {bannerImg ? null : <Text style={{ fontFamily: 'Medium', fontSize: 20, color: textColor, marginBottom: 10 }}>Choose Banner Image (required)</Text>}

                        <TouchableOpacity onPress={pickCoverImage} style={{ marginTop: 'auto', alignItems: 'center', justifyContent: 'center' }}>

                            <ImageBackground style={{ height: 200, width: 200, borderRadius: 100, marginBottom: 20, backgroundColor: backgroundColor, alignItems: 'center', justifyContent: 'center' }} source={{ uri: coverImg ? coverImg.uri : null }}>
                                {coverImg ? null : <Text style={{ fontFamily: 'Medium', fontSize: 15, color: textColor, marginBottom: 10 }}>Choose cover Image (required)</Text>}
                            </ImageBackground>
                        </TouchableOpacity>

                    </ImageBackground>

                </TouchableOpacity>
                <Text style={{ fontFamily: 'Black', fontSize: 50, color: textColor }}>{name}</Text>
                <Text style={{ fontFamily: 'Bold', fontSize: 30, color: textColor }}>{story}</Text>
                <Text style={{ fontFamily: 'Bold', fontSize: 20, color: textColor }}>{desc}</Text>
            </ImageBackground>
            {bannerImg && coverImg ?
                <TouchableOpacity onPress={PublishMovie} style={{ marginTop: 'auto', backgroundColor: textColor, padding: 5, alignItems: 'center', justifyContent: 'center' }} >
                    <Text style={{ fontFamily: 'Regular', fontSize: 20, color: backgroundColor }}>Publish Movie</Text>
                </TouchableOpacity> : null}
        </>
    )
}

const styles = StyleSheet.create({
    overlay: {
        backgroundColor: 'rgba(0,0,0,0.2)',
        flex: 1,
        justifyContent: 'flex-end',
    },
    container: {
        backgroundColor: 'white',
        paddingTop: 12,
        borderTopRightRadius: 12,
        borderTopLeftRadius: 12,
        borderColor: '#2c3e50',
        borderWidth: 1,
    },
    input: {
        padding: 5,
        margin: 5,
        fontFamily: "Medium"
    },
})
